import { useEffect, useState } from "react";
// import DatePicker from "react-datepicker";
import { Col, Container, Row } from 'react-bootstrap';
import { upperFirst } from 'lodash';

import { generateWeathers, Weather } from './weather/Weather';
import { WeatherImage } from './weather/WeatherImage';
import { Region } from './weather/types';

import "react-datepicker/dist/react-datepicker.css";

export function App() {
  // const [startDate, setStartDate] = useState(new Date());
  const [weathers, setWeathers] = useState<Weather[]>([])

  useEffect(() => {
    const weathers = generateWeathers(new Date())
    setWeathers(weathers)
  }, []);

  return (
    <>
      {/* <DatePicker selected={startDate} onChange={(date: Date) => setStartDate(date)} /> */}
      <Container>
        {
          weathers.map((w) =>
            <Row>
              <Col>
                {upperFirst(Region[w.region])}
              </Col>
              <Col>
                {w.weather.name}
              </Col>
              <Col>
                <WeatherImage weather={w.weather.type} />
              </Col>
            </Row>
          )
        }
      </Container>
    </>
  );
}
